import React from 'react';
import { IconButton, Avatar, CardHeader } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

const Medal = (props) => {
    const { onIncrement, onDecrement, country, name, color } = props;

    return (
        <CardHeader avatar={<Avatar alt={name + ' medal count'} sx={{ bgcolor: color }}>
            {
                (country[name].page_value !== country[name].saved_value) ?
                    <span className="delta">{country[name].page_value}</span>
                    :
                    <span>{country[name].page_value}</span>
            }
        </Avatar>}
            title={name + ' medals'} titleTypographyProps={{ variant: 'h5' }}
            action={<div><IconButton color="error" onClick={() => onDecrement(country.id, name)} aria-label='decrease medal count' disabled={country[name].page_value === 0} >
                <RemoveIcon />
            </IconButton>
                <IconButton color="primary" onClick={() => onIncrement(country.id, name)} aria-label='increase medal count'>
                    <AddIcon />
                </IconButton></div>}
        />
    );
}

export default Medal;
