import React from 'react';
import { CardContent, Grid, Card, Divider, Avatar, Typography, IconButton } from '@mui/material';
import Medal from './Medal';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';
import ClearIcon from '@mui/icons-material/Clear';

const Country = (props) => {
    const { onIncrement, onDecrement, country, medals, onDelete, onSave, onReset } = props;

    const getTotalMedals = (country, medals) => {
        let total = 0;
        // medals.forEach(medal => { total += country[medal.name]; });
        medals.forEach(medal => { total += country[medal.name].page_value; });
        return total;
    }
    const renderSaveButton = () => {
        let unsaved = false;
        medals.forEach(medal => {
            if (country[medal.name].page_value !== country[medal.name].saved_value) {
                unsaved = true;
            }
        });
        return unsaved;
    }


    return (
        <Grid item xs={12} sm={6} md={3}>
            <Card className='Country-Card' elevation={6} sx={{ bgcolor: '#FFF' }}>
                <CardContent>
                    <div className='Title-Container'>
                        <Typography gutterBottom variant='h3' component='div'>{country.name}</Typography>
                        <Avatar alt='total medal count' sx={{ bgcolor: '#60F', marginTop: '10px' }}>{getTotalMedals(country, medals)}</Avatar>
                        {renderSaveButton() ?
                            <React.Fragment>
                                <IconButton style={{ marginLeft: '8px' }} onClick={() => onSave(country.id)} aria-label='save medal changes'><SaveIcon/></IconButton>
                                <IconButton stype={{ marginLeft: '8px' }} onClick={() => onReset(country.id)} aria-label='reset medal changes'><ClearIcon/></IconButton>
                            </React.Fragment>
                            :
                            <IconButton color='error' onClick={() => onDelete(country.id)} aria-label='delete country'>
                            <DeleteIcon />
                        </IconButton>
                        }
                    </div>
                    <Divider variant="fullWidth" />
                    {medals.map(medal =>
                        <Medal
                            key={medal.id}
                            color={medal.color}
                            name={medal.name}
                            country={country}
                            onIncrement={onIncrement}
                            onDecrement={onDecrement}
                        />
                    )}
                </CardContent>
            </Card>
        </Grid>
    );
}

export default Country;